import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { IconButton } from '@material-ui/core';
import { Send } from '@material-ui/icons';
import useStyle from './styles';
import { createMessageAction } from '../../../../actions/messageActions';

const ChatController = ({ currentOpenRoom, user }) => {
  const classes = useStyle();
  const dispatch = useDispatch();

  const [message, setMessage] = useState('');
  const [typing, setTyping] = useState(false);

  const socket = useSelector((s) => s.socket);

  const userStatus = useSelector((s) => s.currentChattingUserStatus);
  const { status } = userStatus;

  const handleSubmitMessage = (e) => {
    e.preventDefault();
    if (message) {
      const messageObj = {
        room: currentOpenRoom._id,
        receiver: currentOpenRoom.userToShowOnRoom._id,
        messageTXT: message,
      };
      dispatch(createMessageAction(messageObj));
      setTyping(false);
      setMessage('');
    }
  };

  const handleTypingMessage = (e) => {
    setMessage(e.target.value);
  };

  //? for setting typing variable when typing
  useEffect(() => {
    if (message.trim()) {
      setTyping(true);
    } else {
      setTyping(false);
    }
  }, [message]);
  //! search how to get your socket id then
  //! send it to the user with emitting
  //? for emitting typing event
  useEffect(() => {
    //? here i will set myOnlineId, otherUserOnlineId
    //?
    let otherUserOnlineId;
    if (status) {
      //? here means the change happened while i am online
      if (status === 'online') {
        otherUserOnlineId = userStatus.onlineId;
      } else {
        otherUserOnlineId = userStatus.typingUserOnlineId;
      }
    } else {
      //? here means i became online and i found it
      otherUserOnlineId = currentOpenRoom.userToShowOnRoom.onlineId;
    }
    if (currentOpenRoom.userToShowOnRoom.onlineId && socket.id) {
      if (typing) {
        socket.emit('user-currently-typing', {
          typingUserId: user._id,
          onlineUserId: otherUserOnlineId,
          typingUserOnlineId: socket.id,
        });
      } else {
        socket.emit('user-stopped-typing', {
          typingUserId: user._id,
          onlineUserId: otherUserOnlineId,
          typingUserOnlineId: socket.id,
        });
      }
    }
  }, [currentOpenRoom, socket, status, typing, user, userStatus.onlineId]);

  return (
    <form className={classes.root} onSubmit={handleSubmitMessage}>
      <div className={classes.msgBox}>
        <input
          autoFocus={true}
          placeholder='Type a message'
          type='text'
          className={classes.msgInput}
          rows='1'
          value={message}
          onChange={handleTypingMessage}
        ></input>
      </div>
      <div className={classes.send}>
        <IconButton disabled={!message && true} type='submit'>
          <Send style={{ color: '#9B9B9B' }} />
        </IconButton>
      </div>
    </form>
  );
};

export default ChatController;
