import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  Avatar,
  IconButton,
  ButtonBase,
  Menu,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  DialogContentText,
} from '@material-ui/core';
import Search from '@material-ui/icons/Search';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import useStyle from './styles';
import moment from 'moment';
import {
  deleteRoomAction,
  blockRoomAction,
  unblockRoomAction,
} from '../../../../actions/roomActions';

const Header = ({ currentOpenRoom }) => {
  const classes = useStyle();
  const dispatch = useDispatch();

  const [openDialog, setOpenDialog] = useState(false);

  const userStatus = useSelector((s) => s.currentChattingUserStatus);
  const { status } = userStatus;

  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  const handleDeleteChat = () => {
    dispatch(deleteRoomAction(currentOpenRoom._id));
  };

  const handleBlock = () => {
    dispatch(
      blockRoomAction(currentOpenRoom._id, currentOpenRoom.userToShowOnRoom._id)
    );
    handleCloseMenu();
  };
  const handleUnblock = () => {
    dispatch(
      unblockRoomAction(
        currentOpenRoom._id,
        currentOpenRoom.userToShowOnRoom._id
      )
    );
    handleCloseMenu();
  };

  //? for 3 dots menu
  const [anchorEl, setAnchorEl] = React.useState(null);
  const openMenu = Boolean(anchorEl);
  const ITEM_HEIGHT = 48;

  const handleOpenMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };
  //? end for 3 dots menu
  return (
    <>
      <div className={classes.root}>
        <ButtonBase className={classes.userInfoBtnBase}>
          <div className={classes.userInfo}>
            <div className={classes.avatarContainer}>
              <Avatar src={currentOpenRoom.userToShowOnRoom.photo} />
            </div>
            <div className={classes.info}>
              <div
                className={classes.name}
              >{`${currentOpenRoom.userToShowOnRoom.firstName} ${currentOpenRoom.userToShowOnRoom.lastName}`}</div>
              <div className={classes.status}>
                {status
                  ? status === 'startTyping'
                    ? 'Typing ...'
                    : status === 'online'
                    ? 'Online'
                    : status === 'offline'
                    ? `Last seen at ${moment(userStatus.lastSeenAt).format(
                        'hh:mm A'
                      )}`
                    : status === 'stopTyping'
                    ? 'Online'
                    : ''
                  : ''}

                {!status
                  ? currentOpenRoom.userToShowOnRoom.onlineId
                    ? 'Online'
                    : currentOpenRoom.userToShowOnRoom.lastSeenAt
                    ? `Last seen at ${moment(
                        currentOpenRoom.userToShowOnRoom.lastSeenAt
                      ).format('hh:mm A')}`
                    : ''
                  : ''}
              </div>
            </div>
          </div>
        </ButtonBase>

        <div className={classes.sidebarIcons}>
          <IconButton>
            <Search />
          </IconButton>

          <IconButton onClick={handleOpenMenu}>
            <MoreVertIcon />
          </IconButton>
          <div>
            <Menu
              id='long-menu'
              anchorEl={anchorEl}
              keepMounted
              open={openMenu}
              onClose={handleCloseMenu}
              PaperProps={{
                style: {
                  maxHeight: ITEM_HEIGHT * 4.5,
                  width: '20ch',
                },
              }}
            >
              <MenuItem
                onClick={() => {
                  setOpenDialog(true);
                  handleCloseMenu();
                }}
                className={classes.menuItem}
              >
                Delete Chat
              </MenuItem>
              <MenuItem
                onClick={currentOpenRoom.myBlock ? handleUnblock : handleBlock}
                className={classes.menuItem}
              >
                {currentOpenRoom.myBlock ? 'Unblock' : 'Block'}
              </MenuItem>
            </Menu>
          </div>

          <Dialog
            open={openDialog}
            keepMounted
            onClose={handleCloseDialog}
            aria-labelledby='alert-dialog-slide-title'
            aria-describedby='alert-dialog-slide-description'
          >
            <DialogTitle id='alert-dialog-slide-title'>
              Delete Chat with{' '}
              {currentOpenRoom.userToShowOnRoom.firstName +
                ' ' +
                currentOpenRoom.userToShowOnRoom.lastName}
            </DialogTitle>
            <DialogContent>
              <DialogContentText id='alert-dialog-slide-description'>
                Are you sure you want to deleted this chat.
                <Button variant='text' color='secondary'>
                  Block
                </Button>
                Instead?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDialog} color='primary'>
                No
              </Button>
              <Button
                onClick={() => {
                  handleDeleteChat();
                  handleCloseDialog();
                }}
                color='secondary'
              >
                Yes
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </div>
    </>
  );
};

export default Header;
