import * as c from '../constants/userConstants';
import {
  CURRENT_OPEN_ROOM_RESET,
  UPDATE_CURRENT_OPEN_ROOM,
} from '../constants/roomConstants';
import * as urls from '../constants/urls';
import {
  TYPING_MESSAGE,
  STOP_TYPING_MESSAGE,
} from '../constants/roomConstants';
import axios from 'axios';

export const loginUserAction = (userData) => async (dispatch, getState) => {
  try {
    // const isToken = typeof userData === 'string';
    dispatch({
      type: c.LOGIN_REQUEST,
    });
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };
    const { data } = await axios.post(
      `${urls.usersUrl}/login`,
      userData,
      config
    );

    const { token, user } = data;
    const payload = { ...user, token };

    dispatch({
      type: c.LOGIN_SUCCESS,
      payload,
    });

    localStorage.setItem('loggedInUser', JSON.stringify(payload));
  } catch (err) {
    dispatch({
      type: c.LOGIN_FAIL,
      payload:
        err.response && err.response.data.message
          ? err.response.data.message
          : err.message,
    });
  }
};

export const logoutUser = () => (dispatch, getState) => {
  dispatch({
    type: c.LOGOUT_USER,
  });
  dispatch({
    type: CURRENT_OPEN_ROOM_RESET,
  });
  localStorage.removeItem('loggedInUser');
  localStorage.removeItem('currentRoom');
  const socket = getState().socket;
  socket.disconnect();
};

